.datepicker {
  &.datepicker-dropdown {
    // width: 36.2rem;
    border-radius: 0.2rem;
    border: 0.1rem solid var(--#{$prefix}-color-orange-base);
    box-shadow: 0 0.4rem 2.4rem 0 rgba(0, 0, 0, 0.12);

    &::before,
    &::after {
      display: none;
    }
  }
  &.dropdown-menu {
    padding: 2.4rem;

    th {
      padding: 0;
    }
  }
  &.datepicker-inline {
    width: 100%;
  }

  .prev,
  .next {
    width: 3.2rem;
    height: 3.2rem;
    text-align: center;
    vertical-align: middle;

    &.disabled {
      visibility: visible;
      cursor: default;
      pointer-events: none;

      &:hover,
      &:active {
        background-color: transparent;
      }

      .svg-icon {
        background-color: #d7d5d5;
      }
    }

    .svg-icon {
      width: 2rem;
      height: 2rem;
      @include icon-set("arrow-right");
      background-color: var(--#{$prefix}-color-text-basic);
    }
  }
  .prev {
    .svg-icon {
      transform: rotate(180deg);
    }
  }
  .datepicker-switch {
    width: fit-content;
    height: auto;
    color: #151414;
    font-size: 2.1rem;
    &:hover {
      background-color: transparent;
    }
  }

  table {
    width: 100%;
    thead {
      th {
        &.dow {
          padding: 1.6rem 0 0.4rem;
          width: 4rem;
          height: 3rem;
          color: #757070;
          font-size: 1.4rem;
          font-weight: 400;
        }
      }
    }
    tbody {
      tr {
        td {
          span {
            width: 50%;
            height: 4rem;
            line-height: 4rem;
            margin: 0;
            color: #757070;
            font-size: 1.5rem;
            border-radius: 0.4rem;

            &.disabled,
            &.disabled:hover {
              color: #c3c1c1;
              pointer-events: none;
            }
            &:hover,
            &.focused,
            &.active.active {
              color: #f54500;
              font-weight: 600;
              background-color: #feeae1;
              text-shadow: none;
            }
          }
        }
      }
    }
    tr {
      td.day {
        width: 4rem;
        height: 4rem;
        color: #757070;
        font-size: 1.5rem;

        &.old,
        &.new {
          visibility: hidden;
        }
        &.old, &.new {
          color: #c3c1c1;
        }
        &.disabled,
        &.disabled:hover {
          color: #c3c1c1;
        }
        &.today {
          color: #757070;
          // font-weight: 700;
          background-color: transparent;
          border-color: transparent;

          &.disabled,
          &.disabled:active {
            background-color: transparent;
            color: #c3c1c1;
          }
        }

        &.active.active {
          color: #f54500;
          font-weight: 600;
          background-color: #feeae1;
          border-color: #feeae1;
          text-shadow: none;
        }
      }
    }
    tfoot {
      .today {
        height: 3rem;
        color: #757070;
        font-size: 1.4rem;
        vertical-align: middle;
        background-color: #f3f2f1;
        border-radius: 0.2rem;
      }
    }
  }

  .calendar-today-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 3rem;
    background-color: #f3f2f1;
    color: var(--#{$prefix}-color-text-subtler);
  }
}
